import { autobind } from "@decorators/autobind";
import type { IAdapterData } from "@interfaces/adapter-data";
import type { ICalculatorResult, IPrices } from "@interfaces/calculator-interface";
import type { ICustomizationDetails } from "@interfaces/customization-interface";
import type { IModuleData } from "@interfaces/module-data";

declare const prestashop: any;
declare const TnModules: IAdapterData;

export class Adapter {

  constructor () {
    if (typeof (window as any).TnModules === "undefined") {
      const modules: IAdapterData = {
        list         : [],
        modules      : {},
        prices       : {},
        locks        : {
          price : false,
          cart  : false,
          scroll: false,
        },
        customization: null
      };
      (window as any).TnModules = modules;
    }
    this.registerEvents();
  }

  public getData () {
    return {
      prices: TnModules.prices
    };
  }

  private registerEvents () {
    if (typeof prestashop === "object" && typeof prestashop.on === "function") {
      prestashop.on("updateCart", this.handleAddedToCart);
    }
  }

  public registerModule (dp_id_module: number, data: IModuleData) {
    if (TnModules.list.indexOf(dp_id_module) === -1) {
      TnModules.modules[dp_id_module] = data;
      TnModules.list.push(dp_id_module);
    }
  }

  public registerModuleCallback (id_module, callback: () => Promise<boolean>) {
    TnModules.modules[id_module].customization_callback = callback;
  }

  public registerSyncCallback (id_module, callback: (name: string, value: number) => void) {
    TnModules.modules[id_module].sync_callback = callback;
  }

  public registerResponseCallback (id_module, callback: (response: ICalculatorResult) => void) {
    TnModules.modules[id_module].response_callback = callback;
  }

  @autobind()
  private handleAddedToCart () {
    this.release("cart");
  }

  public isEnabled () {
    return TnModules.list.length > 1;
  }

  public setModulePrices (id_module: number, customization_prices: IPrices) {
    TnModules.prices[id_module] = customization_prices;
  }

  public setModuleResponse (id_current_module: number, response: ICalculatorResult) {
    for (const id_module of TnModules.list) {
      if (id_module !== id_current_module) {
        const moduleData = TnModules.modules[id_module];
        if (moduleData.response_callback) {
          moduleData.response_callback(response);
        }
      }
    }
  }

  public lock (lock_name: string) {
    TnModules.locks[lock_name] = true;
  }

  public release (lock_name: string) {
    TnModules.locks[lock_name] = false;
  }

  public lockRelease (lock_name: string): Promise<void> {
    return new Promise((resolve) => {
      const interval = setInterval(() => {
        if (!TnModules.locks[lock_name]) {
          resolve();
          clearInterval(interval);
        }
      }, 100);
    });
  }

  public isLocked (lock_name: string) {
    return TnModules.locks[lock_name];
  }

  public acquireCartLock () {
    if (this.isLocked("cart")) {
      return false;
    }
    this.lock("cart");
    return true;
  }

  public saveCustomization (response: ICustomizationDetails) {
    TnModules.customization = {
      id_module        : response.id_module,
      id_customizations: response.id_customizations,
    };
  }

  public addCustomizationData (data) {
    if (TnModules.customization) {
      data.id_customizations = TnModules.customization.id_customizations;
    }
  }

  public clearCustomization () {
    TnModules.customization = null;
  }

  public async saveCustomizations (current_id_module: number) {
    for (const id_module of TnModules.list) {
      if (id_module !== current_id_module) {
        const moduleData = TnModules.modules[id_module];
        if (moduleData.customization_callback) {
          const result = await moduleData.customization_callback();
          if (!result) {
            return false;
          }
        }
      }
    }
    return true;
  }

  public notifyValueChange (current_id_module, name, value) {
    for (const id_module of TnModules.list) {
      if (id_module !== current_id_module) {
        const moduleData = TnModules.modules[id_module];
        if (moduleData.sync_callback) {
          moduleData.sync_callback(name, value);
        }
      }
    }
  }

  public scrollToFirstError () {
    if (this.isLocked("scroll")) {
      return false;
    }
    this.lock("scroll");
    const element = $(".tn_invalid:eq(0)").get(0);
    if (element) {
      element.scrollIntoView({ behavior: "smooth", block: "start" });
      this.release("scroll");
    }
  }
}
