import type { IStarter } from "@interfaces/starter-interface"
import { Ajax } from "@tools/ajax"
import { Messenger } from "@tools/messenger"

export class ModuleFormStarter implements IStarter {
  public container: JQuery

  private ajax: Ajax
  private messenger: Messenger

  constructor () {
    this.ajax = new Ajax()
    this.messenger = new Messenger()
  }

  public async handleDocumentReady () {
    this.initModuleForm()
    this.initConfigForm()
  }

  public async handleWindowLoad () {

  }

  public initModuleForm () {
    ($(document) as any).dpLang()
  }

  private initConfigForm () {
    $("#configuration_form").on("change", "input", async (event) => {
      const input = event.currentTarget as HTMLInputElement
      const name = input.name
      const value = input.value
      const data = {
        model : "config",
        action: "save_input_value",
        name  : name,
        value : value
      }
      const response = await this.ajax.send(data)
      this.messenger.showMessage(response)
    })
  }
}
