import type { IAdminMessages } from "@interfaces/admin-messages"

declare var dpa_message: IAdminMessages
declare var showSuccessMessage: (message: string) => void
declare var showErrorMessage: (message: string) => void

export class Messenger {

  public showSuccessMessage (message: string) {
    showSuccessMessage(message)
  }

  public showErrorMessage (message: string) {
    showErrorMessage(message)
  }

  public showMessage (response: any) {
    if (response.success) {
      this.showSuccessMessage(response.message || dpa_message.success)
    } else {
      this.showErrorMessage(response.message || dpa_message.error)
    }
  }
}
