import "../../plugins/lang/jquery.dp_lang"
import { ModuleFormStarter } from "./module-form-starter"

export class ModuleFormBootstrap {

  private document: JQuery<Document>
  private window: JQuery<Window>
  private starter: ModuleFormStarter

  public bootstrap (starter: ModuleFormStarter) {
    this.starter = starter
    this.init()
    this.detectDocumentReady()
    this.detectWindowLoad()
  }

  private init () {
    this.document = $(document)
    this.window = $(window)
  }

  private detectDocumentReady () {
    this.document.ready(() => this.starter.handleDocumentReady())
  }

  private detectWindowLoad () {
    this.window.on("load", () => this.starter.handleWindowLoad())
  }
}
