import { Messenger } from "@tools/messenger"
import { Adapter } from "@views/ts/adapter/Adapter"

const lockMap = {
  "calculateResult": "price",
}

export class Ajax {

  private requests = {}

  private messenger: Messenger
  private adapter: Adapter

  public constructor () {
    this.messenger = new Messenger()
    this.adapter = new Adapter()
  }

  public send (data, can_throw = false, abort_key = null) {
    const promise = $.Deferred()

    const xhr = $.ajax({
      data    : data,
      dataType: "json",
      method  : "post",
      url     : this.getController(data.model)
    })
    if (abort_key) {
      this.abortPreviousRequests(abort_key, xhr)
    }
    $.when(xhr).then((response: any) => {
      this.clearAbortKey(abort_key)
      if (response.error && can_throw) {
        promise.reject(new Error(response.message))
      }
      promise.resolve(response)
    }).fail(() => {
      promise.resolve()
    })
    return promise
  }

  private getController (model: string) {
    const var_name = `dp_${ model }`
    if (typeof window[var_name] !== "undefined") {
      return window[var_name]
    }
    throw new Error("controller not found")
  }

  public async sendWithMessage (data) {
    const response = await this.send(data)
    this.messenger.showMessage(response)
    return response
  }

  private abortPreviousRequests (abort_key: string, Xhr = null) {
    if (this.requests[abort_key]) {
      if (lockMap[abort_key]) {
        this.adapter.release(lockMap[abort_key])
      }
      this.requests[abort_key].abort()
    }
    if (Xhr) {
      this.requests[abort_key] = Xhr
    }
  }

  private clearAbortKey (abort_key: string) {
    this.requests[abort_key] = null
  }
}
